import React from 'react'

export default class QuoteMachine extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            imgWidth: "40px"
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }
    handleMouseEnter() {
        this.setState({
            imgWidth: "45px"
        })
    }
    handleMouseLeave() {
        this.setState({
            imgWidth: "40px"
        })
    }
    render() {
        const quoteMachineStyles = {
            background: "white",
            textAlign: "center",
            height: "auto",
            boxShadow: "0px 0px 30px rgba(0, 0, 0, 0.4)",
            position: "absolute",
            top: "50%",
            transform: "translateY(-50%)",
            width: "100vw",
            padding: "10px 0px 30px 0px",
        }
        const quoteStyles = {
            fontStyle: "italic",
            fontSize: "20px"
        }
        const buttonStyles = {
            fontSize: "15px",
        }
        const imageStyles = {
            paddingTop: "20px",
            width: this.state.imgWidth
        }
        const tweetLink = 'https://twitter.com/intent/tweet?text="' + this.props.quote + '" - ' + this.props.author + '. adoibori.com';
        if (this.props.isLoading) {
            return(
                <div style={quoteMachineStyles}>
                <h1>Quote Machine</h1>
                <p style={quoteStyles}>Fetching...</p>
                <p>Be Patient...</p>
                <button style={buttonStyles} onClick={this.props.handleQuoteButton}>New Quote</button>
                <br/><a rel="noopener noreferrer" target="_blank" href={tweetLink} ><img onMouseLeave={this.handleMouseLeave} onMouseEnter={this.handleMouseEnter} style={imageStyles} alt="twitter link" src="https://www.creativefreedom.co.uk/wp-content/uploads/2017/06/Twitter-featured.png" /></a>
            </div>
            )
        } else {
        return (
            <div style={quoteMachineStyles}>
                <h1>Quote Machine</h1>
                <p style={quoteStyles}>"{this.props.quote}"</p>
                <p>{this.props.author}</p>
                <button style={buttonStyles} onClick={this.props.handleQuoteButton}>New Quote</button>
                <br/><a rel="noopener noreferrer" target="_blank" href={tweetLink} ><img onMouseLeave={this.handleMouseLeave} onMouseEnter={this.handleMouseEnter} style={imageStyles} alt="twitter link" src="https://www.creativefreedom.co.uk/wp-content/uploads/2017/06/Twitter-featured.png" /></a>
            </div>
        )
        }
    }
}
