import React from 'react';
import QuoteMachine from './QuoteMachine';

class App extends React.Component{
  constructor(props) {
    super(props)
    this.state = {
      isLoading: false,
      quotes: {
        quote: "Lorum Ipsum Dolor Set Amor",
        author: "Anonymous"
      },
      color: '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6)
    };
    this.handleQuoteButton = this.handleQuoteButton.bind(this);
  }
  handleQuoteButton() {
    this.setState({ isLoading: true})
    fetch("https://cors-anywhere.herokuapp.com/http://quotes.stormconsultancy.co.uk/random.json")
      .then(response => response.json())
      .then(data => this.setState({
        quotes: data,
        isLoading: false,
        color: '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6),
      }))
  }
  render() {
    const AppStyles = {
      height: "100vh",
      position: "relative",
      background: this.state.color,
      transition: "all .5s ease",
    }
    return (
      <div style={AppStyles}>
        <QuoteMachine handleQuoteButton={this.handleQuoteButton} isLoading={this.state.isLoading} quote={this.state.quotes.quote} author={this.state.quotes.author}/>
      </div>
    );
  }
}

export default App;
